<template>
	<div>
		<h1 class="text-center display-1 deep-orange--text mt-16">Libro id: {{ idCurso }}</h1>

		<v-form v-model="valid" ref="form">
			<v-container>
				<v-row>
					<v-col class="text-center">
						<v-btn class="btn"
						color="light-green darken-1" dark 
						@click="dialog=true"
						>Eliminar Libro</v-btn> 
					</v-col>
					<v-col>
						<v-switch
							v-model="allowEdit"
							label="EDITAR LIBRO"
							color="deep-orange darken-1"
							value="info"
							hide-details
						></v-switch>
                    </v-col>
					<v-col cols="12">
						<v-text-field
							v-model="form.nombre"
							:rules="requiredRules"
							label="Nombre Curso"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.costo"
							:rules="requiredRules"
							:counter="20"
							label="Costo"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.cupos"
							:rules="requiredRules"
							:counter="20"
							label="Cupos"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.descripcion"
							:rules="requiredRules"
							label="Descripción"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12">
						<v-text-field
							v-model="form.duracion"
							:rules="requiredRules"
							label="Duración"
							required
						></v-text-field>
					</v-col>
					
					<v-col cols="12">
						<v-text-field
							v-model="form.estado"
							:rules="requiredRules"
							label="Estado"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.imagen"
							:rules="requiredRules"
							label="Src"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.inscritos"
							:rules="requiredRules"
							:counter="20"
							label="Inscritos"
							required
						></v-text-field>
					</v-col>

					<v-col cols="12">
						<v-text-field
							v-model="form.registro"
							:rules="requiredRules"
							label="Fecha de Registro"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12">
						<v-select 
                            :items="types" 
                            label="Tipo" 
                            v-model="form.type" 
                            :rules="requiredRules"></v-select>
					</v-col>
                   
					<v-col class="text-center">
						<v-btn class="btn" color="deep-orange darken-1" dark :disabled="!allowEdit" @click="editcurso"
							>Guardar Edición
						</v-btn>
					</v-col>
					
					<v-col class="text-center">
                        <!-- <v-btn class="btn"
                        color="deep-orange darken-1" dark 
                        @click="createCurso"
                        >Editar</v-btn> -->
                        <v-btn class="btn"
                        color="grey" dark 
                        @click="resetForm"
                        >Limpiar formulario</v-btn>
                    </v-col>
					
				</v-row>
			</v-container>
		</v-form>
			<v-dialog v-model="dialog" width="500">
				<v-card>
					<v-card-title class="text-h5 grey lighten-2">
						¿Deseas Eliminar el curso?
					</v-card-title>

					<v-divider></v-divider>

					<v-card-actions>
						<v-btn color="deep-orange darken-1" text 
						@click="deletecurso"> Eliminar </v-btn>
						<v-spacer></v-spacer>
						<v-btn color="light-green darken-1" text 
						@click="dialog = false"> Cerrar </v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
	</div>
</template>

<script>
import { mapState } from 'vuex'
import { db } from '@/firebase/firebase.js'
import { doc, getDoc, updateDoc,deleteDoc } from 'firebase/firestore'
export default {
	name: 'curso-comp',
	props: ['idCurso'],
	data: function () {
		return {
			valid: true,
            dialog:false,
			form: {
				nombre: "",
                cupos: 0,
                inscritos: 0,
                duracion: 0,
                costo: 0,
                estado: false,
                registro:"",
                imagen:"",
                descripcion:"",
                type:0,
			},
			requiredRules: [(v) => !!v || 'Campo es requerido'],
			allowEdit: false,
		}
	},
	computed: {
		...mapState(['types']),
	},
	methods: {
		resetForm() {
			this.$refs.form.reset()
		},
		async fetchCursoById() {
			console.log(this.idCurso)
			try {
				const docRef = doc(db, 'cursosAlfaWeb', this.idCurso)
				let response = await getDoc(docRef)
				console.log(response.data())
				this.form = response.data()
			} catch (error) {
				console.log(error)
			}
			
		},
		async editcurso() {
			try {
				let curso = this.form
				curso.id = this.idCurso
				await updateDoc(doc(db, 'cursosAlfaWeb', curso.id), curso)
				this.$router.push('/')
			} catch (error) {
				console.log(error)
			}
		},
        async deletecurso(){
            try{
                await deleteDoc(doc(db,'cursosAlfaWeb',this.idCurso))
                this.$router.push('/')
            }
            catch(error){
                console.log(error)
            }
        },
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	created() {
		this.fetchCursoById()
	},
	// -- End Lifecycle Methods
}
</script>

<style scoped>
form {
	display: grid;
	width: 600px;
	margin: 0 auto;
}
.btn{
  margin: 10px;
}
</style>
