
<template>
	<v-container>
		<h1 class="text-center display-1 deep-orange--text my-12">Agregar nuevo Curso</h1>
	
		<v-form v-model="valid" ref="form">
				<v-row justify="center">
					<v-col cols="9">
						<v-text-field
							v-model="form.nombre"
							:rules="requiredRules"
							label="Nombre Curso"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.costo"
							:rules="requiredRules"
							:counter="20"
							label="Costo"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.cupos"
							:rules="requiredRules"
							:counter="20"
							label="Cupos"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.descripcion"
							:rules="requiredRules"
							label="Descripción"
							required
						></v-text-field>
					</v-col>
					<v-col cols="9">
						<v-text-field
							v-model="form.duracion"
							:rules="requiredRules"
							label="Duración"
							required
						></v-text-field>
					</v-col>
					
					<v-col cols="9">
						<v-text-field
							v-model="form.estado"
							:rules="requiredRules"
							label="Estado"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.imagen"
							:rules="requiredRules"
							label="Src"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.inscritos"
							:rules="requiredRules"
							:counter="20"
							label="Inscritos"
							required
						></v-text-field>
					</v-col>

					<v-col cols="9">
						<v-text-field
							v-model="form.registro"
							:rules="requiredRules"
							label="Fecha de Registro"
							required
						></v-text-field>
					</v-col>
					
					<v-col cols="9">
						<v-select 
                            :items="types" 
                            label="Tipo" 
                            v-model="form.type" 
                            :rules="requiredRules"></v-select>
					</v-col>

                    <v-col cols="9" class="text-center">
                        <v-btn class="btn" 
						small
                        color="light-green darken-1" dark 
                        @click="createCurso"
                        >Agregar</v-btn>
                        <v-btn class="btn"
						x-small
                        color="deep-orange darken-1" dark 
                        @click="resetForm"
                        >Limpiar formulario</v-btn>
						<v-btn class="btn"
						x-small
						color="grey"
						dark
						@click="close">
						Salir/Cancelar
						</v-btn>
                    </v-col>
				</v-row>
		</v-form>
	</v-container>
</template>

<script>
import {mapState} from 'vuex'
import {db} from '@/firebase/firebase.js'
import { collection, addDoc } from 'firebase/firestore'

export default {
	name: 'registerCurso',
	// props: {},
	data: function () {
		return {
			valid: true,
			form: {
				nombre: "",
                cupos: 0,
                inscritos: 0,
                duracion: 0,
                costo: 0,
                estado: false,
                registro:"",
                imagen:"",
                descripcion:"",
                type:0,
			},
			requiredRules: [(v) => !!v || 'Campo es requerido'],
			types:["1", "2"]
		}
	},
	computed: {
		...mapState(['types'])
    },
	methods: {
        async createCurso(){
            this.$refs.form.validate()//validar campos
            if(!this.valid) return //no es valido y funcion se cierra
            try{
                let response =  await addDoc(collection(db,"cursosAlfaWeb"),this.form)
                console.log(response)
                this.$router.push('/')
            }
            catch(error){
                console.log(error)
            }
        },
        resetForm(){
            this.$refs.form.reset()
        },
		close(){
			this.$emit('hideDialog')
		}

		
    }
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
	// En a linea 44 iba originalmente: @click:"createCurso" 
	// En a linea 48 iba originalmente: @click:"resetForm" 
}
</script>

<style scoped>

.btn{
    margin: 10px;
}
</style>
