import Vue from 'vue'
import Vuex from 'vuex'
import { db } from "@/firebase/firebase.js"
import { collection, getDocs} from "firebase/firestore"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cursos:[]
  },
  getters: {
    countCursos(state){
      return state.cursos.length
    },
    estudiantesInscritos(state){ 
      return state.cursos.reduce((total,curso)=> total + Number(curso.inscritos), 0)
    },
    estudiantesCupos(state){ 
      return state.cursos.reduce((total,curso)=> total + Number(curso.cupos), 0)
    },
    cursosTerminado(state){
      let terminados = state.cursos.filter((curso)=>curso.estado==true || curso.estado=='true' )
        return terminados.length
    },
    cursosActivos(state){
      let activos = state.cursos.filter((curso)=>curso.estado==false || curso.estado=='false' )
        return activos.length
    },
  },
  mutations: {
    ADD_CURSO(state,curso){
      state.cursos.push({
        id: curso.id,
        nombre: curso.data().nombre,
        costo: curso.data().costo,
        cupos: curso.data().cupos,
        descripcion: curso.data().descripcion,
        duracion: curso.data().duracion,
        estado: curso.data().estado,
        imagen: curso.data().imagen,
        inscritos: curso.data().inscritos,
        registro: curso.data().registro,
        codigo: curso.data().codigo
        
      })
    },
    RESET_CURSO(state){
      state.cursos = []
    },
  },
  actions: {
    async fetchCursos({commit}){
      let querySnapShot = await getDocs(collection(db,'cursosAlfaWeb'))
      commit('RESET_CURSO')
      console.log(commit)
      querySnapShot.forEach((doc)=>{
        commit('ADD_CURSO',doc)
        console.log(doc.id,'=>',doc.data())
      })
    }
  },
  modules: {
  }
})
