<template>
    <v-container>
    <v-row justify="center">
      <v-col md="6">
        <h1 class="text-h4 my-10 deep-orange--text">Registro de Usuario</h1>
        <v-form>

          <v-text-field class="my-9" v-model="user" type="email" label="Correo">
          </v-text-field>

          <v-text-field v-model="pass" type="password" label="Contraseña">
          </v-text-field>
          <v-btn  class="my-16" @click="register" 
          color="grey" block dark 
          >Registrar 
          </v-btn>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";

export default {
    name: 'register-comp',
    // props: {},
    data: function(){
        return {
            user: "",
            pass: "",
        }
    },
    // computed: {},
    methods: {
        register() {
      const auth = getAuth();
      createUserWithEmailAndPassword(auth, this.user, this.pass)
        .then((userCredential) => {
          // Signed in
            const user = userCredential.user;
            console.log(user)
            this.$router.push('/')
          // ...
        })
        .catch((error) => {
            // const errorCode = error.code;
            const errorMessage = error.message;
            alert(errorMessage)
          // ..
        });
    },
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

