<template>
   <v-container>
        <v-row justify="center">
            <v-col md="6">
                <h1 class="text-h4 mt-10 mb-5 deep-orange--text">Ingreso a Home</h1>
                <p class="mb-16 grey--text">(Debe estar registrado para ingresar a Home)</p>
                <v-form>
                    <v-text-field class="my-5" 
                    v-model="user" 
                    type="email" 
                    label="Correo (user@user.cl)">
                    </v-text-field>

                    <v-text-field 
                    v-model="pass" 
                    type="password" 
                    label="Contraseña (123456)">
                    </v-text-field>
                    <v-btn class="my-16" @click="login"
                    color="grey" dark block
                    >Iniciar
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth'
export default {
    name: 'login-component',
    // props: {},
    data: function(){
        return {
            user:'',
            pass:''
        }
    },
    // computed: {},
    methods: {
        login(){
            const auth = getAuth()
            signInWithEmailAndPassword(auth, this.user, this.pass)
                .then((userCredential) => {
                    // Signed in
                    const user = userCredential.user;
                    console.log(user)
                    this.$router.push('/')
                    // ...
                })
                .catch((error) => {
                    // const errorCode = error.code;
                    const errorMessage = error.message;
                    alert(errorMessage)
                });

        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped>
    h2{
    margin: 40px;
}
</style>