            :items="cursos"
            sort-by="cupos"
            class="elevation-1">
            
            <template v-slot:top>
                <v-toolbar flat class="my-16 ">
                    <v-toolbar-title class="display-2 deep-orange--text">Administración
                    </v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px" persistent>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn color="light-green darken-1" dark class="mb-2" v-bind="attrs" v-on="on" :loading="dialog">
                                Agregar Curso
                            </v-btn>
                        </template> 
                                <!-- Inicio tabla  [\`item.actions\`]-->
                        <v-card>
                            <courses @hideDialog='dialog=false'></courses>
                        </v-card>
                    
                    </v-dialog>

                </v-toolbar>
            </template>
            <template v-slot:[`item.costo`]="{ item }">
                <v-chip
                    color="pink lighten-1"
                    dark>
                    {{ item.costo }}
                </v-chip>
            </template>
            <template v-slot:[`item.registro`]="{ item }">
                <v-chip
                    color="indigo accent-2"
                    dark>
                    {{ item.registro }}
                </v-chip>
            </template>
            <template v-slot:[`item.estado`]="{ item }">
                <v-chip v-if="item.estado==false"
                    color="grey"
                    dark>
                    No
                </v-chip>
                <v-chip v-else
                    color="amber accent-4"
                    dark>
                    Sí
                </v-chip>
            </template>
                <template v-slot:[`item.actions`]="{ item }">
                    <v-icon small class="mr-2" @click="$router.push(`/edit/${item.id}`)"> mdi-pencil </v-icon>
                    <v-icon small @click="$router.push(`/edit/${item.id}`)"> mdi-delete </v-icon>

                    <v-dialog v-model="dialogDelete" max-width="500px">
                        <v-card>
                                <v-card-title class="text-h5"
                                >Estás seguro de borrar el ítem?</v-card-title>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="light-green darken-1" text @click="closeDelete"
                                    >Cancelar</v-btn>
                                <v-btn color="deep-orange darken-3" text @click="deleteItemConfirm"
                                    >OK</v-btn>
                                <v-spacer></v-spacer>
                                </v-card-actions>
                        </v-card>
                    </v-dialog>
                </template>
        </v-data-table>
    
        <v-alert 
        outlined
        color="purple"
        dark
        icon="mdi-account-group"
        >
        <div class="font-weight-small">
        Cantidad total de alumnos permitidos: {{estudiantesCupos}}
        </div>
        </v-alert>

        <v-alert 
        outlined
        color="blue"
        dark
        icon="mdi-account-multiple-check"
        >
        <div class="font-weight-small">
        Cantidad total de alumnos inscritos: {{estudiantesInscritos}}
        </div>
        </v-alert>

        <v-alert 
        outlined
        color="red"
        dark
        icon="mdi-account-clock"
        text
        >
        <div class="font-weight-small">
        Cantidad total de cupos restantes: {{estudiantesCupos-estudiantesInscritos}}
        </div>
        </v-alert>

        <v-alert 
        outlined
        color="pink"
        dark
        icon="mdi-block-helper"
        >
        <div class="font-weight-small">
        Cantidad total de cursos terminados: {{cursosTerminado}}
        </div>
        </v-alert>

        <v-alert 
        text
        outlined
        color="brown"
        dark
        icon="mdi-bell-ring"
        >
        <div class="font-weight-small">
        Cantidad total de cursos activos: {{cursosActivos}}
        </div>
        </v-alert>

        <v-alert 
        outlined
        color="deep-orange"
        dark
        icon="mdi-school"
        >
        <div class="font-weight-small">
        Cantidad total de cursos: {{countCursos}}
        </div>
        </v-alert>
        

    </v-container>
</template>

<script>
import {mapActions,mapState, mapGetters} from 'vuex'
import Courses from '@/components/Courses.vue'
    export default {
    name: "admin-comp",
    data: () => ({
        dialog: false,
        dialogDelete: false,
        headers: [
        {
            text: "Curso",
            align: "start",
            sortable: false,
            value: "nombre",
        },
        { text: "Costo", value: "costo" },
        { text: "Cupos", value: "cupos",width: "5%" },
        { text: "Descripcion", value: "descripcion",width: "20%" },
        { text: "Duración", value: "duracion" },
        { text: "Terminado", value: "estado", sortable: false },
        { text: "Inscritos", value: "inscritos",width: "5%" },
        { text: "Fecha de Registro", value: "registro" },
        { text: 'Actions', value: 'actions', sortable: false },
        ],
        editedIndex: -1,
        editedItem: {
        nombre: "",
        costo: 0,
        cupos: 0,
        descripcion:"",
        duracion: 0,
        estado: false,
        inscritos: 0,
        registro:"",

        },
        defaultItem: {
        nombre: "",
        costo: 0,
        cupos: 0,
        descripcion: 0,
        duracion: "",
        estado: false,
        inscritos: 0,
        registro: 0,
        },
    }),

    components: {
        'courses':Courses
    },
    computed: {
        // formTitle() {
        // return this.editedIndex === -1 ? "New Item" : "Edit Item";
        // },
        ...mapState(['cursos']),
        ...mapGetters(['estudiantesInscritos','countCursos','estudiantesCupos','cursosTerminado','cursosActivos']),
    },

    watch: {
        dialog(val) {
        val || this.close();
        },
        dialogDelete(val) {
        val || this.closeDelete();
        },
    },

    created() {
        this.fetchCursos()
    },

    methods: {
        
        addCurso(){
            this.dialog = true
        },

        editItem(item) {
            this.$router.push('/edit/' + item.id)
            console.log(item)

        // this.editedIndex = this.cursos.indexOf(item);
        // this.editedItem = Object.assign({}, item);
        // this.dialog = true;
        },

        deleteItem(item) {
        this.editedIndex = this.cursos.indexOf(item);
        this.editedItem = Object.assign({}, item);
        this.dialogDelete = true;
        },

        deleteItemConfirm() {
        this.cursos.splice(this.editedIndex, 1);
        this.closeDelete();
        },

        close() {
        this.dialog = false;
        this.$nextTick(() => {
            this.editedItem = Object.assign({}, this.defaultItem);
            this.editedIndex = -1;
        });
        },

        closeDelete() {
        this.dialogDelete = false;
        this.$nextTick(() => {
            this.editedItem = Object.assign({}, this.defaultItem);
            this.editedIndex = -1;
        });
        },

        save() {
        if (this.editedIndex > -1) {
            Object.assign(this.cursos[this.editedIndex], this.editedItem);
        } else {
            this.cursos.push(this.editedItem);
        }
        this.close();
        },
        ...mapActions(['fetchCursos']),
    },
    
    };
</script>